.review {
  display: block;
  margin: 0 20px;

  &__image {
    margin: 0 auto;
    display: block;
    margin-top: 80px;
    width: 85px;
    height: 85px;
  }
  &__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 2.55;
    // text-decoration: 1px solid $underline;
    // text-decoration-line:underline;
    // text-underline-offset:10px;
    text-align: center;
    display: flex;
    justify-content: center;
    &::before {
      background-image: url('../images/svg/symbol-defs.svg#icon-quote-mark');
      width: 54px;
      height: 40px;
      position: relative;
    }
    &::after {
      background-image: repeating-linear-gradient(to bottom, red 30px, blue 1px);
      width: 100%;
    }
  }
  &__background {
    background-color: #fafafa;
    padding: 50px;
    margin-top: 29px;
    margin-bottom: 29px;
  }
  &__dots {
    display: flex;
    justify-content: center;
    max-height: 30px;
  }
  &__data {
    @include review-data;
    margin-bottom: 30px;
  }
  &__home {
    display: flex;
    justify-content: center;
  }
  &__svg {
    max-width: 18px;
  }
}
@media screen and (min-width: 768px) {
  .review {
    &__text {
      font-size: 14px;
    }
    &__data {
      font-size: 14px;
    }
  }
}

@media screen and (min-width: 1200px) {
  .review {
    &__text {
      font-size: 16px;
    }
    &__data {
      font-size: 16px;
    }
  }
}
